
<template>
    <div id="webtemplate">
        <div v-if="!youtubelink">No hay ningun vídeo cargado todavía</div>
        <iframe v-else :src="`https://www.youtube.com/embed/${content.vid}`" frameborder="0" :class="{'videocontent': !isMiniature, 'videominiature': isMiniature}" allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" allowfullscreen style="width: 100%;"></iframe>
        <div v-if="edit">
            <div style="margin-top: 16px;">Introduce el URL de YouTube</div>
            <input type="text" v-model="youtubelink" placeholder="URL de YouTube" style="padding: 16px; font-size: 1.8em; font-weight: light; font-family: 'Nunito'">
        </div>
    </div>
</template>

<script>
export default {
    name: 'web-template',

    data() {
        return {
            youtubelink: ""
        }
    },

    props: {
        content: Object, 
        user: Object, 
        card: Object,
        isMiniature: Boolean,
        edit: Boolean
    },

    watch: {
        youtubelink(value) {
            var regExp = /^.*((youtu.be\/)|(v\/)|(\/u\/\w\/)|(embed\/)|(watch\?))\??v?=?([^#\&\?]*).*/;
            var match = value.match(regExp);
            if ( match && match[7].length == 11 ){
                this.content.vid = match[7];
            }
            else {
                this.content.vid = value
            }
        }
    }
}
</script>

<style>
    .center {
        display: flex;
        flex-direction: column;
        width: 100%;
        height: 100%;
        justify-content: center;
        align-items: center;
    }

    .videominiature {
        height: 250px;
    }

    .videocontent {
        height: 600px;
    }
</style>

